*,*::after,*::before{margin:0;padding:0;box-sizing:inherit}html{font-size:62.5%}@media only screen and (max-width: 75em){html{font-size:56.25%}}@media only screen and (max-width: 56.25em){html{font-size:50%}}@media only screen and (min-width: 112.5em){html{font-size:75%}}body{box-sizing:border-box}::selection{background-color:#ec7819;color:#fff}::-webkit-scrollbar{-webkit-appearance:none}::-webkit-scrollbar:vertical{width:0}::-webkit-scrollbar:horizontal{height:0}::-webkit-scrollbar-thumb{background:#ec7819;border-radius:0.4rem}::-webkit-scrollbar-thumb:hover{background:#ca6716;box-shadow:0 0 2px 1px rgba(0,0,0,0.2)}::-webkit-scrollbar-thumb:active{background-color:#999999}::-webkit-scrollbar-track{background:rgba(255,255,255,0.5)}body{font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-family:'Karla', sans-serif;line-height:1.7;color:#fff}.button{font-weight:bold;height:4rem;line-height:2.5rem;padding:0 3rem;border:0;border-radius:3rem;background-image:linear-gradient(131deg, #ffd340, #ff923c, #ff923c, #ff923c);background-size:300% 100%;transition:all 0.3s ease-in-out}.button:focus{outline:none}.button:hover{outline:none;box-shadow:0 0.5em 0.5em -0.4em #ff923cba;background-size:100% 100%;transform:translateY(-0.15em)}
