*,*::after,*::before{margin:0;padding:0;box-sizing:inherit}html{font-size:62.5%}@media only screen and (max-width: 75em){html{font-size:56.25%}}@media only screen and (max-width: 56.25em){html{font-size:50%}}@media only screen and (min-width: 112.5em){html{font-size:75%}}body{box-sizing:border-box}::selection{background-color:#ec7819;color:#fff}::-webkit-scrollbar{-webkit-appearance:none}::-webkit-scrollbar:vertical{width:0}::-webkit-scrollbar:horizontal{height:0}::-webkit-scrollbar-thumb{background:#ec7819;border-radius:0.4rem}::-webkit-scrollbar-thumb:hover{background:#ca6716;box-shadow:0 0 2px 1px rgba(0,0,0,0.2)}::-webkit-scrollbar-thumb:active{background-color:#999999}::-webkit-scrollbar-track{background:rgba(255,255,255,0.5)}body{font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-family:'Karla', sans-serif;line-height:1.7;color:#fff}.contact{display:flex;justify-content:center;align-items:center;height:100vh}@media only screen and (max-width: 56.25em){.contact{height:90vh;overflow-y:scroll;padding:3rem 0}}.contact .card{margin-top:8rem;width:65%;display:grid;grid-template-columns:repeat(2, 1fr)}@media only screen and (max-width: 56.25em){.contact .card{width:75%;grid-template-columns:repeat(1, 1fr);height:75rem}}.contact__left{display:flex;justify-content:center;align-items:center}.contact__left svg{height:27rem}@media only screen and (max-width: 37.5em){.contact__left svg{height:18rem}}.contact__right{padding:2.5rem 4rem}.contact__right h2{position:relative;padding:0 0 10px;margin-bottom:10px}.form__group:not(:last-child){margin-bottom:2rem}.form__input{font-size:1.5rem;font-family:inherit;color:inherit;padding:1.5rem 2rem;border-radius:2px;background-color:rgba(255,255,255,0.5);border:none;border-bottom:3px solid transparent;border-radius:1rem;width:100%;display:block;transition:all 0.3s}@media only screen and (max-width: 37.5em){.form__input{width:100%}}.form__input:focus{outline:none;box-shadow:0 1rem 2rem rgba(0,0,0,0.1);border-bottom:3px solid #ec7819}.form__input:focus:invalid{border-bottom:3px solid #ec7819}.form__input::-webkit-input-placeholder{color:#333}.form__label{font-size:1.2rem;font-weight:700;margin-left:2rem;margin-top:0.7rem;display:block;transition:all 0.3s}.form__input:placeholder-shown+.form__label{opacity:0;visibility:hidden;transform:translateY(-4rem)}.form__button{width:100%;color:#4f4f4f;margin-top:1rem;border-radius:1rem}#envelope{animation:float 2s ease-in-out infinite}#star1,#star2,#star3,#star4,#star5,#star6{animation:blink 1s ease-in-out infinite}#star2{animation-delay:100ms}#star3{animation-delay:500ms}#star4{animation-delay:700ms}#star5{animation-delay:300ms}#star6{animation-delay:200ms}@keyframes float{0%{transform:translateY(0px)}50%{transform:translateY(-20px)}100%{transform:translateY(0px)}}@keyframes blink{0%{opacity:0}50%{opacity:0.5}100%{opacity:1}}
