*,*::after,*::before{margin:0;padding:0;box-sizing:inherit}html{font-size:62.5%}@media only screen and (max-width: 75em){html{font-size:56.25%}}@media only screen and (max-width: 56.25em){html{font-size:50%}}@media only screen and (min-width: 112.5em){html{font-size:75%}}body{box-sizing:border-box}::selection{background-color:#ec7819;color:#fff}::-webkit-scrollbar{-webkit-appearance:none}::-webkit-scrollbar:vertical{width:0}::-webkit-scrollbar:horizontal{height:0}::-webkit-scrollbar-thumb{background:#ec7819;border-radius:0.4rem}::-webkit-scrollbar-thumb:hover{background:#ca6716;box-shadow:0 0 2px 1px rgba(0,0,0,0.2)}::-webkit-scrollbar-thumb:active{background-color:#999999}::-webkit-scrollbar-track{background:rgba(255,255,255,0.5)}body{font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-family:'Karla', sans-serif;line-height:1.7;color:#fff}.skills-section{display:flex;flex-direction:column;justify-content:center;align-items:center;height:100vh;font-size:2rem}.skills-section h2{text-align:center;font-size:3rem}@media only screen and (max-width: 56.25em){.skills-section{height:89vh;overflow-y:auto}}.skills{display:grid;grid-template-columns:repeat(2, 1fr);grid-gap:3rem}@media only screen and (max-width: 56.25em){.skills{padding:1rem 0;height:90vh;grid-template-columns:repeat(1, 1fr);overflow-y:scroll}}.skills ul li svg{height:8rem;width:8rem}@media only screen and (max-width: 37.5em){.skills ul li svg{height:7rem;width:7rem}}.list{list-style:none;margin:2rem 3rem;padding:2rem 3rem;display:grid;grid-template-columns:repeat(3, 1fr);grid-gap:3rem}@media only screen and (max-width: 37.5em){.list{grid-template-columns:repeat(2, 1fr)}}.list__item{text-transform:uppercase;display:flex;flex-direction:column;justify-content:center;align-items:center}
