*,*::after,*::before{margin:0;padding:0;box-sizing:inherit}html{font-size:62.5%}@media only screen and (max-width: 75em){html{font-size:56.25%}}@media only screen and (max-width: 56.25em){html{font-size:50%}}@media only screen and (min-width: 112.5em){html{font-size:75%}}body{box-sizing:border-box}::selection{background-color:#ec7819;color:#fff}::-webkit-scrollbar{-webkit-appearance:none}::-webkit-scrollbar:vertical{width:0}::-webkit-scrollbar:horizontal{height:0}::-webkit-scrollbar-thumb{background:#ec7819;border-radius:0.4rem}::-webkit-scrollbar-thumb:hover{background:#ca6716;box-shadow:0 0 2px 1px rgba(0,0,0,0.2)}::-webkit-scrollbar-thumb:active{background-color:#999999}::-webkit-scrollbar-track{background:rgba(255,255,255,0.5)}body{font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-family:'Karla', sans-serif;line-height:1.7;color:#fff}.about{display:flex;flex-direction:column;align-items:center;justify-content:center;height:100vh}@media only screen and (max-width: 56.25em){.about{height:90vh;overflow-y:scroll}}.about h2{font-size:4rem}.about__content{width:75%;padding:3rem;display:grid;grid-template-rows:repeat(1, 2fr);grid-template-columns:repeat(2, 1fr)}@media only screen and (max-width: 56.25em){.about__content{padding:0;grid-template-rows:repeat(2, 2fr);grid-template-columns:repeat(1, 1fr);width:90%;height:90vh;overflow-y:scroll}}.about__content-img{display:flex;align-items:center;justify-content:center;width:45rem;margin:0;height:auto}@media only screen and (max-width: 56.25em){.about__content-img{width:100%}}.about__content-img img{padding:0.25rem;background-color:#fff;border:1px solid #dee2e6;border-radius:0.25rem;width:60%}@media only screen and (max-width: 56.25em){.about__content-img img{width:20rem}}.about__content-p{font-size:1.75rem;font-weight:500}
