*,*::after,*::before{margin:0;padding:0;box-sizing:inherit}html{font-size:62.5%}@media only screen and (max-width: 75em){html{font-size:56.25%}}@media only screen and (max-width: 56.25em){html{font-size:50%}}@media only screen and (min-width: 112.5em){html{font-size:75%}}body{box-sizing:border-box}::selection{background-color:#ec7819;color:#fff}::-webkit-scrollbar{-webkit-appearance:none}::-webkit-scrollbar:vertical{width:0}::-webkit-scrollbar:horizontal{height:0}::-webkit-scrollbar-thumb{background:#ec7819;border-radius:0.4rem}::-webkit-scrollbar-thumb:hover{background:#ca6716;box-shadow:0 0 2px 1px rgba(0,0,0,0.2)}::-webkit-scrollbar-thumb:active{background-color:#999999}::-webkit-scrollbar-track{background:rgba(255,255,255,0.5)}body{font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-family:'Karla', sans-serif;line-height:1.7;color:#fff}.home{height:100vh;display:flex;flex-direction:column;justify-content:center;align-items:center;text-shadow:0px 2px 5px rgba(0,0,0,0.4);font-weight:100;letter-spacing:7px}@media only screen and (max-width: 56.25em){.home{height:90vh}}.home h1{font-size:5rem;color:white;text-align:center}.home .sub{font-size:5rem;letter-spacing:2px;color:#ec7819}
