*,*::after,*::before{margin:0;padding:0;box-sizing:inherit}html{font-size:62.5%}@media only screen and (max-width: 75em){html{font-size:56.25%}}@media only screen and (max-width: 56.25em){html{font-size:50%}}@media only screen and (min-width: 112.5em){html{font-size:75%}}body{box-sizing:border-box}::selection{background-color:#ec7819;color:#fff}::-webkit-scrollbar{-webkit-appearance:none}::-webkit-scrollbar:vertical{width:0}::-webkit-scrollbar:horizontal{height:0}::-webkit-scrollbar-thumb{background:#ec7819;border-radius:0.4rem}::-webkit-scrollbar-thumb:hover{background:#ca6716;box-shadow:0 0 2px 1px rgba(0,0,0,0.2)}::-webkit-scrollbar-thumb:active{background-color:#999999}::-webkit-scrollbar-track{background:rgba(255,255,255,0.5)}body{font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-family:'Karla', sans-serif;line-height:1.7;color:#fff}.nav{position:fixed;background-color:#1F2124;transition:width 300ms ease;width:8rem;height:100vh;z-index:1000}.nav:hover{width:18rem}@media only screen and (max-width: 56.25em){.nav:hover{width:100vw}}.nav__links{list-style:none;padding:0;margin:0;display:flex;flex-direction:column;align-items:center;height:100%}.nav__item{width:100%;-webkit-tap-highlight-color:rgba(0,0,0,0)}.nav__item:last-child{margin-top:auto}@media only screen and (max-width: 56.25em){.nav__item:last-child{margin-top:0}}.nav__link{display:flex;align-items:center;height:5.5rem;color:#b6b6b6;text-decoration:none;filter:grayscale(100%) opacity(0.7);transition:600ms;z-index:9999}.nav__link:hover{filter:grayscale(0%) opacity(1);background:#141418;color:#ec7819}.nav__link svg{width:3rem;min-width:4rem;margin:0 1.5rem}.link-text{display:none;margin-left:1.5rem;font-size:1.5rem;font-weight:700}.nav:hover .link-text{display:block}@media only screen and (max-width: 56.25em){.nav:hover .link-text{display:none}}.fa-primary{color:#ff7eee}.fa-secondary{color:#df49a6}.fa-primary,.fa-secondary{transition:600ms}.logo{font-weight:bold;text-transform:uppercase;margin-bottom:1rem;text-align:center;color:#ec7819;background:#141418;font-size:1.5rem;letter-spacing:0.3ch;width:100%}.logo svg{transform:rotate(0deg);transition:600ms}.nav:hover .logo svg{transform:rotate(-180deg)}@media only screen and (max-width: 900px){.nav{bottom:0;width:100vw;height:8rem}.logo{display:none}.nav__links{flex-direction:row}.nav__link{justify-content:center}}
